.App {
  text-align: center;
}

.container-home {
  display: flex;
  text-align: center;
  padding: 20px;
  margin: 20px;
  height: 50vh;

  background-color: #71bca4;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.image-container img {
  max-width: 100%;
  height: auto;
}

.text-container {
  flex: 1;
  padding: 20px;
}

.posts-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 10px;
}

.post-card {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  margin: 10px;
  width: calc(20% - 20px);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.post-card:hover {
  position: relative;
  top: -5px;
  right: 10px;
  color: rgb(0, 0, 0);

  background: linear-gradient(45deg, #e6e6e6, #ffffff 40%, #6aab9a 90%);
  cursor: pointer;
}

.post-card h3 {
  margin: 0;
}

.post-card p {
  margin: 10px 0 0;
}

.title {
  padding: 15px;
}

